import React, { useState } from 'react';

import About from '~/components/About';
import Navbar, { type ActiveSection } from '~/components/Navbar';
import Projects from '~/components/Projects';
import type { VisibilityChangeHandler } from '~/components/Section';
import Skills from '~/components/Skills';

const App = () => {
  const [activeSection, setActiveSection] = useState<ActiveSection>(null);

  const handleVisibilityChange: VisibilityChangeHandler = (ref, isVisible) => {
    if (ref.current && isVisible) {
      setActiveSection(ref.current.id);
    }
  };

  return (
    <React.Fragment>
      <Navbar activeSection={activeSection} />
      <main>
        <About onVisibilityChange={handleVisibilityChange} />
        <Skills onVisibilityChange={handleVisibilityChange} />
        <Projects onVisibilityChange={handleVisibilityChange} />
      </main>
    </React.Fragment>
  );
};

export default App;
