import React, { useEffect, useRef } from 'react';

import useVisible from '~/hooks/useVisible';

type SectionName = 'about' | 'skills' | 'projects' | 'contact';

interface HTMLSectionElement extends HTMLElement {
  id: SectionName
}

type SectionRef = React.MutableRefObject<HTMLSectionElement | null>;

interface VisibilityChangeHandler {
  (ref: SectionRef, isVisible: boolean): void;
}

interface SectionProps extends React.ComponentPropsWithoutRef<'section'> {
  id: SectionName,
  onVisibilityChange: VisibilityChangeHandler;
}

interface SectionWrapperProps {
  onVisibilityChange: VisibilityChangeHandler;
}

const Section = ({ onVisibilityChange, ...props }: SectionProps) => {
  const ref = useRef(null);
  const isVisible = useVisible(ref, {
    // This will therefore break if the size of any section is more than 2x the viewport height
    threshold: 0.5
  });

  useEffect(() => {
    onVisibilityChange(ref, isVisible);
  }, [isVisible]);

  return (
    <section ref={ref} {...props}>
      {props.children}
    </section>
  );
};

export {
  Section as default,
  SectionName,
  SectionProps,
  SectionWrapperProps,
  VisibilityChangeHandler
};
