import React, { useLayoutEffect, useRef, useState } from 'react';

import { animated, useSprings } from '@react-spring/web';
import { useGesture } from '@use-gesture/react';

const items = [1, 2, 3];

const Gallery = () => {
  const index = useRef(0);
  const self = useRef<HTMLDivElement>(null);
  const [width, setWidth] = useState(0);

  const [animationProps, api] = useSprings(items.length, (i) => ({
    display: 'block',
    scale: 1,
    x: i * width
  }), [width]);

  useLayoutEffect(() => {
    if (self.current) {
      setWidth(self.current.offsetWidth);
    }
  }, []);

  const bind = useGesture({

    onDrag: ({
      active, cancel, direction: [xDir], movement: [mx]
    }) => {
      if (active && Math.abs(mx) > width / 2) {
        index.current = Math.max(Math.min(index.current + xDir * -1, items.length - 1), 0);
        cancel();
      }
      api.start((i) => {
        if (i !== index.current) {
          return { display: 'none' };
        }
        const x = (i - index.current) * width + (active ? mx : 0);
        const scale = active ? 1 - Math.abs(mx) / width / 2 : 1;
        return { display: 'block', scale, x };
      });
    }
  });

  return (
    <div className="gallery" ref={self}>
      {width && animationProps.map(({ display, scale, x }, i) => (
        // eslint-disable-next-line react/no-array-index-key
        <animated.div {...bind()} className="gallery-item" key={i} style={{ display, x }}>
          <animated.div style={{ scale }}>
            <h1>
              Item
              { i }
            </h1>
          </animated.div>
        </animated.div>
      ))}
    </div>
  );
};

export default Gallery;
